.header {
  width: 100vw;
  height: 100px;
  background-color: #ff6349;
  display: flex;
  align-items: center;
  align-items: center;
  justify-content: space-around;
}

.header__navs {
  display: flex;
  color: #ffffff;
  align-items: center;
}
.header__navs a:hover:not(:first-child) {
  text-decoration: underline;
  cursor: pointer;
}
.header__navs a:not(:last-child) {
  margin-right: 18px;
}

.header__socialIcons .fab:not(:last-child) {
  margin-right: 20px;
}
.header__socialIcons {
  color: #ffffff;
  font-size: 30px;
  display: flex;
}

.header__socialIcons {
  font-size: 30px;
  cursor: pointer;
}

.header__navs p.large {
  font-size: 40px;
  letter-spacing: 2px;
  padding-right: 25px;
}
.fontdiff {
  font-family: "Architects Daughter", cursive;
}
