.banner {
  height: 100vh;
  width: 100vw;
  background-image: url("https://images.unsplash.com/photo-1512255631150-62ae26eb1966?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80");
  background-size: cover;
  background-repeat: no-repeat;
  object-fit: contain;
  opacity: 0.9;
}
.banner__heading {
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  opacity: 1;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.banner__heading p {
  font-size: 100px;
  position: relative;
  opacity: 1 !important;
  top: -30px;
  font-family: "Architects Daughter", cursive;
  color: #ffffff;
  animation-duration: 1s;
  animation-name: slidefromleft;
}

@keyframes slidefromleft {
  0% {
    transform: translateY(10%);
    opacity: 0;
  }
  80% {
    transform: translateY(-5%);
    opacity: 0.8;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}
